import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class TransactionsService {

  constructor(
    public db: AngularFirestore,
  ) { }

  getTransactions() {
    return this.db.collection("Transactions").valueChanges();
  }
}
