import { Injectable } from '@angular/core';
import { AngularFireMessaging } from '@angular/fire/messaging';
import { AngularFireFunctions } from '@angular/fire/functions';
import { ToastController } from '@ionic/angular';
import { tap } from 'rxjs/operators';
import * as firebase from 'firebase';
import { Observable } from 'rxjs';
import { firebaseConfig } from '../../../firebaseConfig';



@Injectable({
  providedIn: 'root'
})
export class FcmService {

  token;

  constructor(
    private afMessaging: AngularFireMessaging,
    private fun: AngularFireFunctions,
    private toastController: ToastController
  ) {
    // firebase.initializeApp(firebaseConfig);
    const _messaging = firebase.messaging();
    _messaging.onTokenRefresh = _messaging.onTokenRefresh.bind(_messaging);
    _messaging.onMessage = _messaging.onMessage.bind(_messaging);

  }


  async makeToast(message) {
    const toast = await this.toastController.create({
      message,
      duration: 5000,
      position: 'top',
      showCloseButton: true,
      closeButtonText: 'dismiss'
    });
    toast.present();
  }


  getPermission() {
    return this.afMessaging.requestToken.subscribe(snap => {
      this.fun.httpsCallable('subscribeToAdminNotifocations')({ topic: "adminNotifications", token: snap })
        // .pipe(tap(_ => this.makeToast(`Notifications Enabled`)))
        .subscribe();
    })

  }
  subscribeToTopic() {
  }
  showMessages(): Observable<any> {
    return this.afMessaging.messages.pipe(
      tap(msg => {
        const body: any = (msg as any).notification.title;
        this.makeToast(body);
      })
    );
  }


}
