import { Component, OnInit } from '@angular/core';
import { CategoriesService } from '../../../../Services/Categories/categories.service';
import { ModalController, NavParams } from '@ionic/angular';
import { CommonService } from '../../../../Services/Common/common.service';

@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html',
  styleUrls: ['./edit-category.component.scss'],
})
export class EditCategoryComponent implements OnInit {

  cat = this.navParams.get("cat");
  img1;
  img2;

  constructor(
    public catService: CategoriesService,
    public commonService: CommonService,
    public navParams: NavParams,
    public modalCtrl: ModalController,
  ) {
    this.catService.category.patchValue(this.cat)
    this.img1 = this.cat.imageUrl;

  }
  ngOnInit() { }


  async updateCat() {
    let data = this.catService.category.value;
    data.id = this.cat.id;
    if (data.name) {
      if (data.imageUrl) {
        this.catService.updateWithNewImage(data, this.img2, this.cat).then(() => { this.close(); });
      } else {
        if (this.img2) {
          this.catService.updateWithNewImage(data, this.img2, this.cat).then(() => { this.close(); });
        } else {
          this.commonService.presentToast("Select an image for the Category")
        }
      }
    } else { this.commonService.presentToast("Enter Name of the Category") }
  }


  fileChange(event) {
    if (event.target.files && event.target.files[0]) {
      let reader = new FileReader();

      reader.onload = (event: any) => {
        this.img1 = event.target.result;
      }
      reader.readAsDataURL(event.target.files[0]);
    }
    let fileList: FileList = event.target.files;
    let file: File = fileList[0];
    this.img2 = file;
  }


  removeImage() {
    this.img1 = null;
    this.catService.category.patchValue({ imageUrl: null });
  }




  close() {
    this.modalCtrl.dismiss();
  }
}
