import { Component, OnInit } from '@angular/core';
import { PaymentsService } from '../../../Services/PaymentService/payments.service';
import { Observable } from 'rxjs';
import { CommonService } from '../../../Services/Common/common.service';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-payment-pages',
  templateUrl: './payment-pages.component.html',
  styleUrls: ['./payment-pages.component.scss'],
})
export class PaymentPagesComponent implements OnInit {

  payments: Observable<any>;
  profits;
  showLoader: boolean = true;

  constructor(
    public paymentService: PaymentsService,
    public navCtrl: NavController,
    public commonService: CommonService,
  ) {
    this.getPayments();
    this.getProfits();
  }

  ngOnInit() { }
  async getProfits() {
    this.commonService.getProfits().subscribe(snap => {
      this.profits = snap.payload.data();
    });
  }

  getPayments() {
    this.payments = this.paymentService.getPayments();
    this.payments.subscribe(() => this.showLoader = false);
  }

  filterPayments(status) {
    if (status) {
      this.getPaymentsbyStatus(status);
    } else {
      this.getPayments();
    }
  }

  sellerDetail(id) {
    this.navCtrl.navigateForward(`/seller-details/${id}`)
  }


  getPaymentsbyStatus(status) {
    this.showLoader = true;
    if (status == "Pending") {
      this.payments = this.paymentService.getPendingPayments();
    }
    if (status == "Paid") {
      this.payments = this.paymentService.getPaidPayments();
    }
    this.payments.subscribe(() => { this.showLoader = false });
  }

}
