import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { SellersService } from '../../../Services/sellers/sellers.service';
import { MenuController, NavController } from '@ionic/angular';

@Component({
  selector: 'app-sellers',
  templateUrl: './sellers.component.html',
  styleUrls: ['./sellers.component.scss'],
})
export class SellersComponent implements OnInit {
  name: string = 'Sellers';

  sellers: Observable<any>;
  showLoader: boolean = false;
  totSellers: number = 0;

  startAt: number = 1;
  endAt: number = 5;
  pageMargin: number = 1;
  constructor(
    public sellerService: SellersService,
    public menuCtrl: MenuController,
    public navCtrl: NavController,
  ) {
    // this.sellerService.addFakeSellers();
    this.menuCtrl.enable(true);
  }

  ngOnInit() {
    this.getSellers();
    this.getTotalSellers();
  }

  getSellers() {
    this.showLoader = true;
    this.sellers = this.sellerService.getSellers();
    this.sellers.subscribe(() => { this.showLoader = false });
  }
  getSellerbyStatus(status) {
    this.showLoader = true;
    this.startAt = 1;
    this.endAt = 5;
    this.sellers = this.sellerService.getSellersbyStatus(status, this.startAt, this.endAt);
    this.sellers.subscribe(() => { this.showLoader = false });
  }
  sellerDetail(id) {
    this.navCtrl.navigateForward(`/seller-details/${id}`)
  }

  filterSellers(status) {
    if (status) {
      this.getSellerbyStatus(status);
    } else {
      this.getSellers();
    }
  }

  async getTotalSellers() {
    this.sellerService.getSellers().subscribe(snap => {
      this.totSellers = snap.length;
      if (this.endAt > this.totSellers) { this.endAt = this.totSellers; }
    })
  }

  goFwrd() {
    this.startAt = this.startAt + this.pageMargin;
    this.endAt = this.endAt + this.pageMargin;
    if (this.endAt > this.totSellers) { this.endAt = this.totSellers; }
    this.getSellers();
  }
  goBack() {
    this.startAt = this.startAt - this.pageMargin;
    this.endAt = this.endAt - this.pageMargin;
    if (this.endAt > this.totSellers) { this.endAt = this.totSellers; }
    this.getSellers();
  }

}


