import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { AlertController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class NotificationsService {

  constructor(
    public db: AngularFirestore,
    public alertCtrl: AlertController,
  ) {

  }

  getNotifications() {
    return this.db.collection("AdminNotifications").snapshotChanges()
  }
  getUnreadNotifications() {
    return this.db.collection("AdminNotifications", ref => ref.where("status", "==", "Unread")).snapshotChanges()
  }

  async viewSingleNotification(noti) {
    const alert = await this.alertCtrl.create({
      header: noti.head,
      message: noti.body,
      buttons: [
        {
          text: 'Done',
          handler: () => {
          }
        }
      ]
    });

    await alert.present();
    return this.markAsRead(noti.id)
  }

  async markAsRead(id) {
    await this.db.collection("AdminNotifications").doc(id).set({ status: "Read" }, { merge: true });
  }

}
