import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import * as firebase from 'firebase';
import { FormGroup, FormControl, Validators } from '@angular/forms';


@Injectable({
  providedIn: 'root'
})
export class OrdersService {

  constructor(
    public db: AngularFirestore,
  ) { }

  orders = new FormGroup({
    status: new FormControl("")
  });

  getAllAOrders() {
    return this.db.collection("Orders").snapshotChanges()
  }
  getStatusAOrders(status) {
    return this.db.collection("Orders", ref => ref.where("status", "==", status)).snapshotChanges()
  }
  deliverOrder(prod) {
    return this.db.collection("Orders").doc(prod.id)
      .set({ status: "Completed" }, { merge: true })
      .then(() => {
        this.db.collection("Sellers").doc(prod.storeId).update(
          { dueProfits: firebase.firestore.FieldValue.increment(prod.vendorProfit) })
      }).then(() => {
        this.db.collection("AdminData").doc("Profits").update({
          vendorCredit: firebase.firestore.FieldValue.increment(prod.vendorProfit),
        })
      })
  }

  getPendingOrders() {
    return this.db.collection("Orders", ref => ref.where("storeId", "==", firebase.auth().currentUser.uid).where("status", "==", "Pending")).snapshotChanges()
  }
}
