import { Component, OnInit } from '@angular/core';
import { BulkNotificationService } from '../../../Services/Messaging/Notifications/bulk-notification.service';
import { CommonService } from '../../../Services/Common/common.service';
import { FcmService } from '../../../Services/Notifications/fcm/fcm.service';

@Component({
  selector: 'app-send-bulk-notifications',
  templateUrl: './send-bulk-notifications.component.html',
  styleUrls: ['./send-bulk-notifications.component.scss'],
})
export class SendBulkNotificationsComponent implements OnInit {

  constructor(
    public messagingService: BulkNotificationService,
    public commonService: CommonService,
    public fcm: FcmService,
  ) { }

  ngOnInit() { }

  sendNotification() {
    if (this.messagingService.notification.valid) {
      this.messagingService.sendBulkNotification(this.messagingService.notification.value).then(() => {
        this.messagingService.notification.reset();
        this.commonService.presentToast("Notification Sent");
      })
    } else {
      this.commonService.presentToast("Notification not valid");
    }
  }

}
