import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as firebase from 'firebase';
import * as  moment from 'moment';
@Injectable({
  providedIn: 'root'
})
export class SellersService {

  status = new FormGroup({
    name: new FormControl("")
  });


  constructor(
    private db: AngularFirestore,
  ) { }

  getSellers() {
    return this.db.collection("Sellers").snapshotChanges()
  }
  getPaginatedSellers(startAt, endAt) {
    return this.db.collection("Sellers", ref => ref.orderBy("order").startAt(startAt).endAt(endAt)).snapshotChanges()
  }
  getallOrders() {
    return this.db.collection("Orders").snapshotChanges();
  }
  getSellersbyStatus(status, startAt, endAt) {
    return this.db.collection("Sellers", ref => ref.where("Status", "==", status).orderBy("order").startAt(startAt).endAt(endAt)).snapshotChanges()
  }
  getSingleSeller(id) {
    return this.db.doc(`Sellers/${id}`).snapshotChanges();
  }

  verifySeller(id) {
    return this.db.doc(`Sellers/${id}`).set({ Status: "Verified" }, { merge: true });
  }

  unverifySeller(id) {
    return this.db.doc(`Sellers/${id}`).set({ Status: "Unverified" }, { merge: true });
  }


  // addFakeSellers() {
  // tempArr.forEach(snip => {
  //   return this.db.collection(`Sellers`).add(snip);
  // })
  // }

  // delseller(id) {
  //   return this.db.doc(`Sellers/${id}`).delete();
  // }


  paySeller(amt, sellerId, storeName) {
    let amount: number = +amt;
    return this.db.doc(`Sellers/${sellerId}`).set(
      {
        dueProfits: firebase.firestore.FieldValue.increment(-amount),
        totalProfits: firebase.firestore.FieldValue.increment(amount)
      }, { merge: true }).then(() => {
        this.db.collection("AdminData").doc("Profits").update({
          vendorCredit: firebase.firestore.FieldValue.increment(-amount),
        }).then(() => {
          this.db.collection("Transactions").add({
            storeName: storeName,
            amount: amount,
            sellerId: sellerId,
            timestamp: moment().format()
          })
        })
      })
  }

}
