import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SellersService } from '../../../Services/sellers/sellers.service';
import { ProductsService } from '../../../Services/products/products.service';
import { Observable } from 'rxjs';
import { NavController, AlertController } from '@ionic/angular';
import { CommonService } from '../../../Services/Common/common.service';
import * as firebase from 'firebase';
import { AuthService } from 'src/app/Services/Auth/auth.service';

@Component({
  selector: 'app-seller-detail',
  templateUrl: './seller-detail.component.html',
  styleUrls: ['./seller-detail.component.scss'],
})
export class SellerDetailComponent implements OnInit {
  name: string = 'Seller Details';
  docId;
  seller;
  showLoader: boolean = false;
  products: Observable<any>;
  orders: Observable<any>;
  verified: boolean;
  admin;
  constructor(
    private router: ActivatedRoute,
    public sellerService: SellersService,
    public prodService: ProductsService,
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public commonService: CommonService,
    public authService: AuthService,
  ) { }

  ngOnInit() {
    this.router.params.subscribe(params => {
      this.docId = params['id'];
    });
    this.getSeller();
    this.getProducts();
    this.getOrders();
    this.getAdmin();
  }


  getSeller() {
    this.showLoader = true;
    this.sellerService.getSingleSeller(this.docId).subscribe(snap => {
      this.seller = snap.payload.data();
      this.seller.id = snap.payload.id;
      if (this.seller.Status == "Verified") { this.verified = true } else { this.verified = false }
      this.showLoader = false;
    });
  }
  getProducts() {
    this.products = this.prodService.getProductbySeller(this.docId);
  }
  getOrders() {
    this.orders = this.prodService.getOrdersbySeller(this.docId);
  }

  productDetail(prodId) {
    this.navCtrl.navigateForward(`/product-details/${prodId}`)
  }

  getAdmin() {
    firebase.auth().onAuthStateChanged((user: firebase.User) => {
      if (user) {
        this.authService.getProfile(user.uid).subscribe(snap => {
          this.admin = snap.payload.data();
        })
      }
    });
  }

  async getAdminPass() {
    const alert = await this.alertCtrl.create({
      header: "Enter Admin's Password",
      inputs: [
        {
          name: 'pass',
          type: 'password',
          placeholder: 'Admin Password',
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: (blah) => {
          }
        }, {
          text: 'Verify',
          handler: data => {
            console.log(data.pass)
            console.log(this.admin)
            if (data.pass == this.admin.Password) {
              this.viewPass();
            } else {
              this.commonService.presentToast("Password not valid");
            }
            this.alertCtrl.dismiss();
          }
        }
      ]
    });
    return await alert.present();
  }

  async viewPass() {
    const alert = await this.alertCtrl.create({
      header: this.seller.storeName.toUpperCase() + "'s" + 'password is "' + this.seller.pass + '"',
      buttons: [
        {
          text: 'Done',
          handler: data => {
            this.alertCtrl.dismiss();
          }
        }
      ]
    });
    return await alert.present();
  }

  async payTheDue() {
    const alert = await this.alertCtrl.create({
      header: this.seller.storeName,
      message: "Pending Amount is" + " " + this.seller.dueProfits,
      inputs: [
        {
          name: 'amount',
          type: 'number',
          placeholder: 'Amount Paid',
          min: '0',
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: (blah) => {
          }
        }, {
          text: 'Paid',
          handler: data => {
            if (data.amount) {
              this.sellerService.paySeller(data.amount, this.docId, this.seller.storeName);
            } else {
              this.commonService.presentToast("Quantity not Valid");
            }
            this.alertCtrl.dismiss();
          }
        }
      ]
    });
    return await alert.present();

  }
  async confirmVerify() {
    const alert = await this.alertCtrl.create({
      header: this.seller.storeName,
      message: "Verify this Seller ?",
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: (blah) => {
          }
        }, {
          text: 'Verify',
          handler: data => {
            this.verifySeller();
          }
        }
      ]
    });
    return await alert.present();
  }

  async confirmUnVerify() {
    const alert = await this.alertCtrl.create({
      header: this.seller.storeName,
      message: "Unverify this Seller ?",
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: (blah) => {
          }
        }, {
          text: 'Unverify',
          handler: data => {
            this.UnverifySeller();
          }
        }
      ]
    });
    return await alert.present();
  }

  verifySeller() {
    this.sellerService.verifySeller(this.docId).then(() => {
      this.getSeller();
      this.commonService.presentToast("Seller Verified")
    })
  }
  UnverifySeller() {
    this.sellerService.unverifySeller(this.docId).then(() => {
      this.getSeller();
      this.commonService.presentToast("Seller Unverified")
    })
  }


}
