import { Component, OnInit } from '@angular/core';
import { OrdersService } from '../../../Services/Orders/orders.service';
import { Observable } from 'rxjs';
import { NavController, AlertController } from '@ionic/angular';
import { CommonService } from '../../../Services/Common/common.service';

@Component({
  selector: 'app-pending-orders',
  templateUrl: './pending-orders.component.html',
  styleUrls: ['./pending-orders.component.scss'],
})
export class PendingOrdersComponent implements OnInit {

  orders: Observable<any>;
  showLoader: boolean = false;
  totData: number = 0;
  constructor(
    public orderService: OrdersService,
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public commonService: CommonService,
  ) {
    this.getOrders();
  }

  ngOnInit() {
    this.orderService.orders.patchValue({ status: "" })
  }

  filterOrder(catId) {
    if (catId) {
      this.getOrderBySatus(catId);
    } else {
      this.getOrders();
    }
  }

  getOrderBySatus(catId) {
    this.showLoader = true;
    this.orders = this.orderService.getStatusAOrders(catId);
    this.orders.subscribe(snap => { this.showLoader = false; this.totData = snap.length; });
  }

  getOrders() {
    this.showLoader = true;
    this.orders = this.orderService.getAllAOrders();
    this.orders.subscribe(snap => { this.showLoader = false; this.totData = snap.length; });
  }
  gtStore(order) {
    window.location.href
  }

  async deliver(prodId, prod) {
    let tempProd = prod;
    tempProd.id = prodId;
    const alert = await this.alertCtrl.create({
      header: 'Deliver the order ?',
      subHeader: 'Is the order completed ? ',
      buttons: [
        {
          text: 'Not Delivered',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
          text: 'Delivered',
          handler: () => {
            this.orderService.deliverOrder(tempProd)
              .then(() => {
                this.commonService.presentToast("Order Completed");
              })
            this.alertCtrl.dismiss();
          }
        }
      ]
    });

    await alert.present();

  }
}
